<template>
<div>
	<h1></h1>
    <div>{{time}}</div>	
</div>
</template>

<script>
	export default{
		name:'DateClockOne',
		data(){
			return {
				time:''
			}
		},
		mounted:function(){
			this.showTime();
		},
		methods:{
			showTime(){
				var myDate = new Date(),
                    year = myDate.getFullYear(),
                    month = myDate.getMonth(),
                    date = myDate.getDate(),
                    weekday = new Array(7),
                    d = myDate.getDay(),
                    h = myDate.getHours(),
                    m = myDate.getMinutes(),
                    s = myDate.getSeconds();
	            m = this.checkTime(m);
	            s = this.checkTime(s);
	            weekday[0] = '';
	            weekday[1] = '';
	            weekday[2] = '';
	            weekday[3] = '';
	            weekday[4] = '';
	            weekday[5] = '';
	            weekday[6] = '';

            	this.time = year+''+month+''+date+' '+weekday[d]+h+':'+m+':'+s;
            	setTimeout(this.showTime,500);
	           
	             /*console.log(': '+myDate.getFullYear());
		        console.log(': '+(myDate.getMonth()+1));
		        console.log(': '+myDate.getDate());
		        console.log(': '+myDate.getDay());
		        console.log(': '+myDate.getHours());
		        console.log(': '+myDate.getMinutes());
		        console.log(': '+myDate.getSeconds());
		        console.log(': '+myDate.getTime());*/

			},
			checkTime(i){
				 if(i<10){
	                i = '0'+i;
	            }
	            return i;
			}
		}
	}
</script>

<style>
</style>


